		},
		methods: {
			sub: function() {
				if(this.user == 123456 && this.pasw == 123456){
					this.$router.push({ path:'/workbench',query:{login:true} })
				}
			},
			getCode:function(data){
				alert(data)
			},
		}
	}
</script>

